.header-search-wrapper.search-wrapper-wide.search {
	&::before {
		display: none;
	}
}
.home-page-nine,
.main.category-page {
	.menu-with-slider {
		z-index: 4;
		// height: 720px;
		.menu-slider.row {
			width: 100%;
			height: max-content;
			margin: 0 auto;
			.menu-wrapper {
				width: 100%;
				height: 720px;
				overflow-x: auto;
				&::-webkit-scrollbar {
					width: 2px;
				}
				&::-webkit-scrollbar-thumb {
					background-color: #aaa;
				}
				.menu-heading {
					margin: 0 0 20px 0;
					font-weight: bold;
				}
				.menu-list {
					.menu_item {
						position: relative;
						justify-content: space-between;
						a {
							padding: 14px 0;
							display: flex;
							align-items: center !important;
							font-size: 16px;
							line-height: 19px;
							color: #000000;
						}
						&:hover {
							cursor: pointer;
						}
						.picture {
							// display: none;
							//////////////volidam////////////
							width: 22px;
							height: 22px;
							margin-right: 10px;
							img {
								object-fit: contain;
							}
						}
						&::after {
							background: none repeat scroll 0 0 transparent;
							bottom: 0;
							content: "";
							display: block;
							height: 0.5px;
							left: 0;
							position: absolute;
							background: $primary-color;
							transition: width 0.3s ease 0s, left 0.3s ease 0s;
							width: 0;
						}
						&:hover::after {
							width: 95%;
						}
					}
				}
			}
			.banners-childmenus {
				position: relative;
				padding: 0 !important;
				width: 100%;
				height: 100%;
				/////allmax
				.background,
				.child-menu-list {
					position: absolute;
					width: 100%;
					margin: 0;
					z-index: 2 !important;

					padding: 10px;
					max-height: 720px;
					overflow: auto;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-thumb {
						background-color: #eee;
						border-radius: 20px;
					}

					.menu-item.col-lg-4 {
						flex-grow: 1;
						padding-left: 0;
						font-size: 16px;
						height: max-content;
						margin-bottom: 10px;
						.menu-link {
							font-weight: bold;
							color: black;
							align-items: center;
							overflow: hidden;
							white-space: nowrap;
							text-overflow: ellipsis;
							display: block;
							img {
								// display: none !important;
								////////////volidam,munique,telefonchi///////
								width: 25px;
								height: 25px;
								display: inline;
								object-fit: cover;
								border-radius: 50%;
								overflow: hidden;
								margin-right: 10px;
							}
							&:hover {
								color: $primary-color;
							}
						}
						.menu-list {
							li {
								margin: 10px 0;
								overflow: hidden;
								width: 100%;
								a {
									display: block;
									overflow: hidden;
									white-space: nowrap;
									text-overflow: ellipsis;
									font-size: 16px;
									font-weight: normal !important;
									color: #002930;
									&:hover {
										color: $primary-color !important;
									}
								}
							}
						}
					}
				}

				.background {
					height: 720px;
					border-radius: 5px;
					border: 1px solid #e5e8eb;
					background-color: #fff;
				}
			}
		}
		.col-12 {
			// height: 100%;
			.splide.big-slider {
				border-radius: $border-radius-blocks;
				overflow: hidden;
				height: 100%;
				// max-height: 400px;
				/////others
				max-height: 515px;
				///tstore
				// max-height: 500px;
				///////////telefonchi
				.splide__track {
					height: 100%;
					.intro-slide {
						height: 100%;
						img {
							height: 100%;
							object-fit: cover;
						}
					}
				}
			}
		}
	}
	.banners {
		.first {
			padding-left: 0;
			.col-lg-12 {
				margin-bottom: 10px !important;
				.banner-one {
					margin-bottom: 0 !important;
					border-radius: 5px;
					overflow: hidden;
					// height: 160px;
					///////////others
					// height: 205px;
					//////////telefonchi
					height: 240px;
					//////////tstore
					// height: auto;

					img {
						object-fit: cover;
					}
				}
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		.first.col-lg-6 {
			.col-lg-12 {
				&:last-child {
					margin-bottom: 0 !important;
				}
			}
		}
		.second {
			display: flex;
			justify-content: space-between;
			padding: 0;
			.col-6:first-child {
				padding-right: 5px !important;
			}
			.col-6:last-child {
				padding-left: 5px !important;
			}
			.banner-one {
				// height: 330px;
				///////////otehers
				// height: 420px;
				//////telefonchi
				height: 490px;
				//////tstore
				// height: auto;
				/////tstore

				border-radius: 5px;
				overflow: hidden;

				img {
					object-fit: cover;
				}
			}
		}
	}
	.collection-tabs.container {
		margin-top: 40px;
		.collectionv1 {
			.nav.nav-pills {
				.nav-item {
					margin-left: 62px;
					.nav-link {
						padding: 0;
						font-weight: bold;
						font-size: 22px;
						line-height: 32px;
						color: $body-text;
						padding-bottom: 8px;
					}
					&:first-child {
						margin-left: 0;
					}
				}
				.nav-item.show {
					.nav-link {
						color: $primary-color;
						&::before {
							height: 0.2rem;
						}
					}
				}
			}
			.splide__track {
				.splide__slide {
					padding-bottom: 20px !important;
				}
			}
		}
	}
	.container.mini-slider {
		margin-top: 44px;
		.splide.mini-slider.splide--slide {
			.splide__list {
				.splide__slide {
					display: flex;
					border: 1px solid #f0f0f0;
					border-radius: 5px;
					padding: 5px;
					.banner-one {
						width: 90px;
						height: 70px;
						margin-right: 16px;
						img {
							object-fit: cover;
							border-radius: 5px;
						}
					}
					.slider-title {
						margin: 0px !important;
						display: flex;
						align-items: center;
						font-size: 18px;
						line-height: 24px;
						font-weight: 600;
					}
				}
			}
		}
	}
	.collections-three {
		.only-desktop {
			.product-item {
				width: 23.5%;
				margin: 20px 10px 0 0;
			}
		}

		.title {
			margin: 40px 0 20px 0;
			font-weight: bold;
			font-size: 30px;
		}
	}
	.banners-container-second {
		margin-top: 42px;
		.banners {
			.col-md-3.only-desktop {
				display: none !important;
			}
			.col-md-9.d-flex {
				min-width: 100%;
				.first {
					width: 64%;
					display: flex;
					justify-content: space-between;
					.banner-one:first-child {
						width: 64%;
					}
					.banner-one:last-child {
						width: 34%;
					}
				}
				.second {
					width: 34.5%;
					.banner-one:first-child {
						margin-bottom: 20px;
					}
				}
			}
		}
	}
	.collections-three {
		.product-item {
			padding: 0;
		}
	}
}

.main.category-page {
	.page-content {
		.row {
			margin: 0 auto;
		}
		.menu-with-slider {
			left: 0;
			height: 500px;
			.menu-slider.row {
				position: absolute;
				left: 0;
				.menu-wrapper {
					height: 500px;
				}
				.banners-childmenus {
					.background,
					.child-menu-list {
						max-height: 500px;
					}
				}
				.background {
					height: 500px;
				}
			}
		}
	}

	.col-lg-9.skeleton-body.skel-shop-products {
		.menu-with-slider {
			position: absolute;
			width: 100%;
		}
	}
}
.home-page-nine,
.product-page-six,
.main.category-page,
.choosen-product-list,
.main.supplier-page,
.trademark-page,
.search-result-page {
	.row.align-items-center.w-100 {
		margin: 0 auto;
	}
	.card-two.card-six {
		margin-bottom: 20px;
		padding: 14px;
		background: #fff;
		border-radius: $border-radius-blocks;
		transition: 0.4s;
		.product-header {
			height: 200px;
			img {
				transition: 0.4s;
				object-fit: contain;
			}
			.discount {
				display: flex !important;
				right: 0px;
				left: unset;
				z-index: 3;
				background: #fd6b6b;
			}
			.favourites-button,
			.compare-button {
				position: unset;
			}
		}

		&:hover {
			box-shadow: rgba(0, 0, 0, 0.1) -4px 9px 25px -6px;
			.product-header img {
				transform: scale(1.01);
			}
		}
		.body-product {
			.price-title {
				display: flex;
				flex-direction: column-reverse;
				margin-top: 10px;
				height: auto;
			}
			.title-product {
				height: 40px;
				a {
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}
			.price-product {
				margin: 6px 0;
				display: flex;
				align-items: center;
				.price-new {
					font-size: 18px;
					line-height: 22px;
				}
				.price-old {
					font-size: 14px;
					line-height: 17px;
				}
				.price-new.discount,
				.price-old {
					color: $dark-text;
				}
			}

			.brand {
				display: none;
			}
			.sale-with-cart {
				.action-product {
					width: 100%;
					.cart-count.d-none {
						font-size: 12px;
						width: 20px;
						height: 20px;
						background: $primary-color;
						color: $light-text;
						border-radius: 50%;
						margin-left: 9px;
						display: flex !important;
						align-items: center;
						justify-content: center;
					}
					.cart-btn {
						border: 1px solid $primary-color;
						background: transparent;
						color: $primary-color;

						width: 100%;
						display: flex;
						align-items: center;
						justify-content: center;
						font-weight: normal;
						font-size: 16px;
						line-height: 19px;
						transition: 0.3s;
						img {
							margin-right: 9px;
							display: inline;
						}

						&:hover {
							img {
								filter: brightness(0) invert(1);
							}
							background: $primary-color;
							color: $light-text;
							span {
								background: $light-text;
								color: $primary-color;
							}
						}
					}
					.cart-btn.added {
						background: #52c41a;
						color: $light-text;
						&:hover {
							img {
								filter: unset;
							}
						}
					}
				}
				.sale {
					display: none;
				}
				/////////
				// .product-details-quantity {
				// 	background: transparent;
				// 	border: 1px solid $primary-color;
				// }
				///////////telefonchi
			}
		}
	}
}
.main.supplier-page {
	.page-content {
		.skel-shop-products {
			.category-title {
				display: none;
			}
			.category-toolbox {
				.filter-b {
					display: none;
				}
				.sort-b {
					min-width: 100%;
					.toolbox {
						width: 100%;
						select {
							color: $body-text;
						}
						.toolbox-layout {
							display: none;
						}
					}
				}
			}
		}
		.skel-shop-sidebar {
			.sidebar.sidebar-shop {
				.filter-price {
					div {
						button {
							display: none;
						}
					}
				}
			}
		}
	}
}
.product-page-six {
	.category-header {
		display: none;
	}
	.page-content {
		margin-top: 20px;
		.product-details-top {
			.product-details-footer {
				.supliers-row {
					.social-icon {
						border: none;
						color: $primary-color;
					}
				}
			}
		}
	}
	.card-two.card-six {
		.product-header {
			height: 235px;
		}
	}
}

.category-active {
	max-width: 100%;
	min-width: 100%;
	header {
		display: none;
	}
	.mobile-menu-container {
		position: unset !important;
		left: unset !important;
		max-width: 100% !important;
		visibility: unset !important;
		height: 100vh;
		// z-index: 10000;
		padding-bottom: 80px;
	}

	.mobile-menu-wrapper {
		position: relative;
		.back-block {
			z-index: 2;
			right: 15px;
			top: 15px;
			position: absolute;

			img {
				width: 22px;
				filter: brightness(0) invert(0);
				////////////////allmax,volidam,agera,tstore//////////////
			}
		}

		.for-home-six {
			display: none;
		}
	}
}

.main.category-page,
.trademark-page {
	margin-top: 24px;
	.products {
		margin-top: 20px;
	}
	.breadcrumb-nav {
		display: none;
	}
	.toolbox {
		margin: 0;
		.toolbox-center {
			display: flex;
			.canceler-button {
				max-width: 200px;
				display: flex;
				align-items: center;
				border-radius: $border-radius-blocks;
				& > span {
					overflow: hidden;

					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
		}
	}
	.skel-shop-sidebar {
		.sidebar.sidebar-shop {
			.widget {
				.widget-side {
					.widget.widget-collapsible {
						.widget-title {
							a {
								width: 100%;
								display: flex;
								align-items: center;
								justify-content: space-between;
								&::after {
									content: "\E802";
								}
								&.collapsed::after {
									content: "\F110";
								}
								a {
									width: fit-content;
									padding-right: 10px;
									svg {
										path {
											fill: $body-text;
										}
									}
									&::after {
										display: none;
									}
								}
							}
						}
					}
				}
			}
		}
	}

	.page-content {
		.col-lg-9.skeleton-body.skel-shop-products {
			padding-left: 32px;
			.product-toolbox {
				display: none;
			}
			.category-title.only-mobile {
				display: inline !important;
				font-weight: bold;
				font-size: 26px;
				line-height: 32px;
				margin-bottom: 18px;
			}
		}
		.col-lg-3.skeleton-body {
			.sidebar.sidebar-shop {
				.filter-price {
					display: none;
				}

				.widget.widget-collapsible {
					.widget-title {
						a {
							font-weight: bold;
							font-size: 16px;
							line-height: 22px;
						}
					}
					.widget-body {
						.filter-item {
							label {
								font-size: 13px;
								line-height: 19px;
							}
						}
					}
				}
			}
		}
	}
}
.dashboard {
	.user-cards-list {
		.title-add-card {
			button {
				svg {
					path {
						fill: $light-text !important;
					}
				}
				color: $light-text !important;
			}
		}
	}
}
.footer {
	margin-top: 20px !important;
	.footer-middle {
		padding-bottom: 40px !important;
		background-color: $primary-color !important;
		.footer-info {
			.widget.widget-about {
				.footer-logo {
					// width: 80px;
					/////////others
					width: 200px;
					///////munique,telefonchi,tstore
				}
				.social-links-block.d-none,
				.widget-email.d-none,
				.widget-call.d-none {
					display: flex !important;
				}
				.widget-call.d-none {
					border: none;
					padding: 0;
					margin: 42px 0 16px 0;
				}
				.widget-email.d-none {
					a {
						font-size: 18px;
						line-height: 24px;
					}
				}
				.social-links-block.d-none {
					a {
						margin: 24px 24px 0 0;
						img {
							width: 25px;
						}
					}
				}
				.payment-methods {
					display: none;
				}
			}
		}
		.widget.contacts {
			.contact_list {
				a {
					display: block;
					white-space: nowrap;
					margin-bottom: 0.4rem;
				}
			}
			.footer-social-links.d-flex {
				display: none !important;
			}
		}
	}
	.footer-bottom {
		.container {
			&::before {
				display: none;
			}
			.d-flex.footer-bottom-wrap {
				justify-content: space-between;
			}
		}

		background: $primary-color-dark !important;
		.bottom-links,
		.copy-right {
			display: none;
		}

		.d-flex.footer-bottom-wrap {
			justify-content: space-between;
			align-items: center;
			.d-none {
				display: flex !important;
				color: $light-text;
				img {
					width: 60px;
					margin-left: 20px;
				}
			}
		}
	}
}
#login-modal,
.login {
	.form-box {
		.tab-content {
			form {
				.person-type {
					display: none !important;
				}
			}
		}
	}
}
.shipping_service_modal.pickup {
	max-width: 1200px !important;
	bottom: unset !important;

	.shipping_service_modal_content {
		width: 100%;

		.row.locations-map {
			margin: 0 auto;
			width: 100%;
			.locations {
				height: 400px;
				overflow-y: auto;
				.location {
					display: block;
					padding: 10px;
					width: 100%;
					border-radius: $border-item;
					border: 1px solid $body-text;
					margin-bottom: 10px;
					transition: 0.3s;
					&:hover {
						background: $primary-color;
						color: $light-text;
					}
				}
				.location.active {
					background: $primary-color;
					color: $light-text;
				}
			}
			.map {
				height: 400px;
				.map-body,
				.map_content {
					height: 100%;
				}
			}
		}
	}
}
.shipping_service_modal_content,
.comment-modal,
.map-modal-two,
.cash-div,
.add_new_card,
.my-modal-content {
	margin: 20px auto !important;
	width: 600px;
	background: #ffffff;
	border-radius: 20px;
	animation-name: translateBottomToTop;
	// animation-duration: 0.4s;
	animation-fill-mode: forwards;
	padding: 20px;
	margin: 0 auto !important;

	.btn.btn-primary,
	.btn {
		margin-top: 12px;
		width: 100% !important;
		padding: 16px;
		border-radius: 10px;
		font-weight: bold;
		font-size: 20px;
		line-height: 22px;
		color: #ffffff !important;
	}
}
.header-seven {
	.header-middle.sticky-header {
		background: $primary-color !important;
		///////////////////////////////////////////////parfumkz,telefonchi,tstore,Gbox,volidam,munique///////////////////////////
		padding: 16px 0 !important;
		////////////others//////////
		// padding: 0 !important;
		/////////munique/////
		margin-bottom: 20px;
		.right-header {
			.dropdown-toggle,
			.choosen-products-icon,
			.profile-link {
				img {
					// filter: brightness(0) invert(0) !important;
					//////////////////////////////////////////volidam,Allmax////////////////////
					filter: brightness(0) invert(1) !important;
					//////////////////////////////gbox,posuz,munique,tsore,parfumkz/////////////////

					///telefonchi/////////////////
				}
			}
			.cart-count,
			.choosen-products-icon span {
				background: $light-text !important;
				color: $primary-color !important;
			}
		}
