			);
			box-shadow: 0px 2px 14px rgba(0, 0, 0, 0.25);
			border-radius: 20px;
			width: 70px;
			height: 70px;
			margin-top: -28px;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			span {
				margin-top: 5px;
				font-size: 9px;
				line-height: 11px;
				color: #ffffff;
			}
		}
		li.active {
			img {
				filter: unset;
			}
			& > span {
				color: $primary-color;
			}
		}
	}
}

